<template>
  <div class="container-fluid">
    <div class="row" id="page">
      <div class="col-md-3 d-sm-none d-md-block " id="sidebar">
        <Sidebar :details="details" v-if="details" />
        <!-- <FooterNote /> -->
      </div>
      <b-modal
        v-cloak
        title="Welcome"
        v-model="modalShow"
        hide-header-close
        no-close-on-backdrop
        no-close-on-esc
        hide-footer
        hide-header
      >
        <div class="col-12">
          <div v-if="visitorName && isCookieExist" class="mt-4">
            <h5 class="ml-2">
              You're back {{ visitorName }}, its good to see you again.
            </h5>
          </div>
          <div v-else>
            <h4>Welcome</h4>
            <p class="ml-2">Hi I'm Mikhael and you are?</p>
            <div class="form-group">
              <input
                type="text"
                v-model="visitorName"
                class="nameInput form-control form-control-sm"
                placeholder="What do you want me to call you"
                autofocus="autofocus"
                v-on:keyup.enter="proceed()"
              />
              <div
                :class="`invalid-feedback ${formValid ? `d-none` : `d-block`}`"
              >
                I would really love to know who you are
              </div>
            </div>
          </div>
          <button class="btn btn-primary btn-sm mt-3" @click="proceed()">
            Proceed
          </button>
        </div>
      </b-modal>
      <div class="col-md-9 black-bg" id="content">
        <div
          v-if="!details && initFirebase"
          class="d-flex justify-content-center align-items-middle align-items-center"
          style="height: 100vh"
        >
          <AtomSpinner
            :animation-duration="1000"
            :size="100"
            :color="'#fffffff'"
          />
          <h5 class="ml-2">
            Hi {{ visitorName }},<br />
            Preparing firebase data...
          </h5>
        </div>
        <!-- <HomePage id="home" /> -->
        <div v-if="details">
          <About id="about" :details="details" />
          <Experience id="experience" :details="details" />
          <Skills id="skills" :details="details" />
          <Portfolio id="portfolio" />
          <Contact id="contact" :details="details" />
        </div>
      </div>
    </div>
  </div>
</template>
<style>
