  border: 0;
  border-bottom: 1px solid #ccc;
  border-radius: 0;
  outline: none;
}
input.nameInput:focus {
  outline: none;
  border: 0;
  border-bottom: 1px solid #ccc;
  box-shadow: none;
}
</style>

<script>
import { AtomSpinner } from "epic-spinners";
import Sidebar from "./Sidebar";

import About from "../Pages/About";
import Experience from "../Pages/Experience";
import Skills from "../Pages/Skills";
import Portfolio from "../Pages/Portfolio";
import Contact from "../Pages/Contact";
import browser from "browser-detect";
import axios from "axios";
// const axios = require("axios");
/**
 * Firebase Config
 */

const fireConfig = {
  apiKey: "AIzaSyA0QlEllyYvDHnfq5eMESP4PG1ZNVOArhc",
  authDomain: "portfolio-511f5.firebaseapp.com",
  databaseURL: "https://portfolio-511f5.firebaseio.com",
  projectId: "portfolio-511f5",
  storageBucket: "portfolio-511f5.appspot.com",
  messagingSenderId: "29893582718"
};

// eslint-disable-next-line no-undef
// const fire = null;
const fire = firebase.initializeApp(fireConfig);
const brow = browser();

export default {
  data() {
    return {
      isCookieExist: null,
      modalShow: true,
      initFirebase: false,
      visitorName: null,
      formValid: true,
      details: null,
      visitors: null,
      visitor: {
        name: "Unknown Visitor",
        browser: brow.name,
        version: brow.version,
        os: brow.os,
        date: new Date()
      }
    };
  },
  async created() {
    let mikkahVisitor = await this.getCookie("mikkahVisitor");
    this.visitorName = mikkahVisitor ? mikkahVisitor : null;
    this.isCookieExist = (await this.getCookie("mikkahSite"))
      ? await this.getCookie("mikkahSite")
      : null;
  },
  methods: {
    async proceed() {
      this.formValid = false;
      if (this.visitorName != null && this.visitorName != "") {
        this.initFirebase = true;
        this.formValid = true;
        this.modalShow = false;
        document.cookie = "mikkahVisitor=" + this.visitorName;
        if (fire) {
          this.visitor.name = this.visitorName;
          await this.loadFirebase();
        }
      }
    },
    async loadFirebase() {
      let isCookieExist = await this.getCookie("mikkahSite");
      // eslint-disable-next-line no-undef
      // await this.getDetails();
      fire
        .database()
        .ref("Details")
        .on("value", snapshot => {
          this.details = snapshot.val();
        });

      if (!isCookieExist && this.visitorName) {
        document.cookie = "mikkahSite=mikkah.github.io";
        let ipadd = await axios.get("https://api.ipify.org/");
        let visitDetails = {
          IP: ipadd.data
        };
        // Firebase visitor key
        let newVisitorKey = fire
          .database()
          .ref("visitors")
          .push().key;

        // Data
        let visitors = {};
        visitors[newVisitorKey] = { ...this.visitor, ...visitDetails };

        fire
          .database()
          .ref("visitors")
          .update(visitors);
      }
      await this.getVisitors();
    },
    async getCookie(cname) {
      let name = cname + "=";
      let decodedCookie = decodeURIComponent(document.cookie);
      let ca = decodedCookie.split(";");
      for (let i = 0; i < ca.length; i++) {
        let c = ca[i];
        while (c.charAt(0) == " ") {
          c = c.substring(1);
        }
        if (c.indexOf(name) == 0) {
          return c.substring(name.length, c.length);
        }
      }
      return "";
    },
    async getDetails() {
      fire
        .database()
        .ref("Details")
        .on("value", snapshot => {
          this.details = snapshot.val();
        });
    },
    async getVisitors() {
      fire
        .database()
        .ref("visitors")
        .on("value", snapshot => {
          this.visitors = snapshot.val();
        });
    }
  },
  components: {
    AtomSpinner,
    Sidebar,
    About,
    Experience,
    Skills,
    Portfolio,
    Contact
  }
};
</script>
