<template>
  <div>
    <div class="row">
      <div class="col-md-12">
        <div class="mt-5">
          <h1 class="text-warning">Where I've been.</h1>
        </div>
      </div>
    </div>
    <div class="row">
      <div class="col-md-12">
        <div class="timeline mt-1">
          <Timeline
            :timeline-items="timelineItems"
            :message-when-no-items="messageWhenNoItems"
          />
        </div>
      </div>
    </div>
  </div>
</template>
<style scoped>
.timeline {
  /* margin-left: 10%; */
}
</style>

<script>
import "timeline-vuejs/dist/timeline-vuejs.css";
import Timeline from "timeline-vuejs";

export default {
  props: ["details"],
  created() {
    let timelineItems = [];

    this.details.experience.forEach(val => {
      timelineItems.push({
        from: new Date(val.year, val.month - 1),
        description: val.details
      });
    });
    this.timelineItems = timelineItems;
  },
  data() {
    return {
      messageWhenNoItems: `There aren't items`,
      timelineItems: []
    };
  },
  components: {
    Timeline
  }
};
</script>
